.contacts {

  &__content {

    @include onTablet {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 20px;
    }

    @include onDesktop {
      column-gap: 198px;
    }
  }

  &__form {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}

.form {

  &__field {
    display: block;
    height: 48px;
    width: 100%;
    padding: 14px 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    border: 1px solid transparent;
    border-radius: 40px;
    background: #f8f8fa;
    color: $page-color-secondary;
    box-sizing: border-box;

    &--textarea {
      height: 148px;
      font-family: "Poppins", sans-serif;
      border-radius: 16px;
      resize: none;
    }

    &::placeholder {
      color: #7e7e83;
    }

    &:hover {
      border-color: #d5d7de;
    }

    &:focus {
      border-color: $page-color-secondary;
      outline: none;
    }
  }
}

.info {

  &__list {
    margin: 0;
    padding: 0;
  }

  &__item {
    margin-bottom: 24px;
    list-style-type: none;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__title {
    margin: 0 0 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }

  &__link {
    display: block;
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
    text-decoration: none;
    @include transition;
    color: $text-color;

    &:hover {
      color: $text-color-hover;
    }
  }
}
